<template>
  <div class="box">
    <mt-header title="选择预约体检单位">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="top">
      <img class="image" src="../assets/adress.png" alt="">
      <div class="p" v-if="address==''">正在定位...</div>
      <div class="p" v-else>{{address}}</div>
    </div>
    <el-amap vid="amap" :plugin="plugin" class="amap-demo" :center="center">
    </el-amap>
    <ul class="list">
      <li class="lists" @click="listClick(item.ID)" v-for="item in hospitalList">
        <div class="left">
          <p class="p1">{{item.Name}}</p>
          <p class="p2">{{item.Address}}</p>
          <p class="p3"></p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import { Indicator,Toast } from 'mint-ui'
  const axios = require('axios')
   export default{
    data () {
      let self = this
      return {
        center: [121.59996, 31.197646],
        lng: 0,
        lat: 0,
        loaded: false,
        address: '',
        hospitalList: [],
        plugin: [{
          pName: 'Geolocation',
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  console.log(result)
                  self.lng = result.position.lng
                  self.lat = result.position.lat
                  self.center = [self.lng, self.lat]
                  self.loaded = true
                  self.address = result.addressComponent.province + result.addressComponent.city + result.addressComponent.district
                  self.$nextTick()
                }
              })
            }
          }
        }]
      }
    },
    methods: {
      listClick(index) {
        this.$router.push({
						path: '/time',
						query: {listIndex:index}
					})
      },
      GetHospitalList() {
        axios({
          method: 'post',
          url: 'https://www.c65z45.cn/api/Reserve/GetHospitalList',
          data: {
            Token: 'B68DE9D1F8584D7099BCE47144D0351C'
          }
        }).then(res => {
          Indicator.close()
          if(res.data.IsSuccess){
            this.hospitalList = res.data.Data
          }else{
            Toast({
              message: '加载失败',
              position: 'middle',
              duration: 3000
            })
          }
        }).catch(error => {
          Indicator.close()
          Toast({
            message: '服务器错误',
            position: 'middle',
            duration: 3000
          })
        })
      }
    },
    created() {
      this.GetHospitalList()
    },
    mounted(){
      Indicator.open('加载中...')
    }
  }
</script>
<style scoped>
  .mint-header {
    background: rgb(0, 204, 153);
  }

  .box {
    width: 100%;
    height: 100vh;
    background: rgb(243, 243, 243);
  }

  .amap-demo {
    height: 0px;
  }

  .top {
    width: 100%;
    height: 1rem;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0.7rem;
  }

  .top .p {
    font-size: 0.25rem;
    color: rgb(40, 177, 160);
  }

  .top .image {
    width: 0.45rem;
    height: 0.47rem;
    padding-right: 0.1rem;

  }

  .list {
    margin-top: 0.3rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #fff;
  }

  .list .lists {
    box-sizing: border-box;
    width: 90%;
    height: 2.36rem;
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center;
  }

  .list .lists .left {
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .list .lists .left .p1 {
    font-size: 0.35rem;
    color: #000;
  }

  .list .lists .left .p2 {
    font-size: 0.3rem;
    color: rgb(102, 102, 102);
  }

  .list .lists .left .p3 {
    font-size: 0.25rem;
    color: rgb(135, 135, 135);
  }

  .list .lists .right {
    width: 20%;
    height: 80%;
    text-align: right;
    line-height: 2rem;
    font-size: 0.3rem;
    color: rgb(209, 161, 83);
  }
</style>
